<template>
<div class="hello">
   <mt-header title="" v-if="shenfen != ''">
        <mt-button icon="back" slot="left" @click="backFn">返回</mt-button>
    </mt-header>
    <div class="btnbox" v-if="shenfen == ''">
        <mt-radio
            title="请选择您的身份"
            v-model="shenfen"
            :options="shenfenList">
        </mt-radio>
    </div>

    <div class="athlete" v-if="shenfen == '运动员'">
        <h4 class="title">散打与自由搏击竞赛机制的区别及致胜因素研究（运动员）</h4>
        <div class="card">
            <p>尊敬的运动员：</p>
            <p>你好。</p>
            <p>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;进行关于《散打与自由搏击竞赛机制的区别及致胜因素研究》论文。特制订了本调查问卷，请您根据自己的实际情况如实填写，以利于我们了解真实的情况，更有利于本论文的编著。恳请您在百忙之中给予帮助。非常感谢。
            </p>
        </div>
        <div class="main">
            <mt-radio
                title="您的性别"
                v-model="athlete.xingbie"
                :options="options1">
            </mt-radio>
            <mt-radio
                title="您的学历"
                v-model="athlete.xueli"
                :options="options2">
            </mt-radio>
            <mt-radio
                title="您的运动等级"
                v-model="athlete.rank"
                :options="options3">
            </mt-radio>
            <p class="mint-radiolist-title">您感觉影响你比赛发挥的个人因素主要有哪些</p>
            <textarea placeholder="请填写" rows="4" class="mint-field-core" v-model="athlete.factor" style="border:1px solid #ebeef5;border-right-width:0;border-left-width:0;"></textarea>
        </div>
    </div>

    <div class="coach" v-if="shenfen == '教练员'">
        <h4 class="title">散打与自由搏击竞赛机制的区别及致胜因素研究（教练员）</h4>
        <div class="card">
            <p>尊敬的教练员：</p>
            <p>你好。</p>
            <p>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;进行关于《散打与自由搏击竞赛机制的区别及致胜因素研究》论文。特制订了本调查问卷，请您根据自己的实际情况如实填写，以利于我们了解真实的情况，更有利于本论文的编著。恳请您在百忙之中给予帮助。非常感谢。
            </p>
        </div>
        <div class="main">
            <mt-radio
                title="您的性别"
                v-model="coach.xingbie"
                :options="options1">
            </mt-radio>
            <mt-radio
                title="您的学历"
                v-model="coach.xueli"
                :options="options2">
            </mt-radio>
            <mt-radio
                title="您的运动等级"
                v-model="coach.rank"
                :options="options3">
            </mt-radio>
            <mt-radio
                title="您对运动员的训练方法"
                v-model="coach.fangfa"
                :options="options4">
            </mt-radio>
        </div>
    </div>

    <div class="referee" v-if="shenfen == '裁判员'">
        <h4 class="title">散打与自由搏击竞赛机制的区别及致胜因素研究（裁判员）</h4>
        <div class="card">
            <p>尊敬的裁判员：</p>
            <p>你好。</p>
            <p>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;进行关于《散打与自由搏击竞赛机制的区别及致胜因素研究》论文。特制订了本调查问卷，请您根据自己的实际情况如实填写，以利于我们了解真实的情况，更有利于本论文的编著。恳请您在百忙之中给予帮助。非常感谢。
            </p>
        </div>
        <div class="main">
            <mt-radio
                title="您的性别"
                v-model="referee.xingbie"
                :options="options1">
            </mt-radio>
            <mt-radio
                title="您的学历"
                v-model="referee.xueli"
                :options="options2">
            </mt-radio>
            <mt-radio
                title="您的裁判等级"
                v-model="referee.rank"
                :options="options5">
            </mt-radio>
        </div>
    </div>
    
    <div v-if="shenfen != ''" class="suBtn" @click="suBtnFn">
        <mt-button type="primary">提交</mt-button>
    </div>
</div>
</template>

<script>
import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
export default {
  name: "HelloWorld",
  data() {
    return {
      shenfen: "",
      athlete: {
        xingbie: "",
        xueli: "",
        rank: "",
        factor: ""
      },
      coach: {
        xingbie: "",
        xueli: "",
        rank: "",
        fangfa: ""
      },
      referee: {
        xingbie: "",
        xueli: "",
        rank: ""
      },
      shenfenList: ["运动员", "教练员", "裁判员"],
      options1: ["男", "女"],
      options2: ["高中", "大中专", "本科", "初中及以下"],
      options3: ["国家一级", "国家二级", "无级别"],
      options4: [
        "有计划周期训练",
        "无计划周期训练",
        "因人而异针对性训练",
        "其他"
      ],
      options5: ["国家级裁判员", "国家一级裁判员", "国家二级裁判员及以下"]
    };
  },

  methods: {
    backFn() {
      this.shenfen = "";
    },

    suBtnFn() {
      if (this.shenfen == "运动员") {
        if(this.jiaoyan(this.athlete)){
            var list = JSON.parse(localStorage.getItem('athlete')) || [];
            list.push(this.athlete);
            list = JSON.stringify(list);
            localStorage.setItem('athlete',list);
            Toast({
                message: "提交成功！",
                position: "middle",
                duration: 3000
            });
            this.shenfen = "";
        }
      } else if (this.shenfen == "教练员") {
        if(this.jiaoyan(this.coach)){
            var list = JSON.parse(localStorage.getItem('coach')) || [];
            list.push(this.coach);
            list = JSON.stringify(list);
            localStorage.setItem('coach',list);
            Toast({
                message: "提交成功！",
                position: "middle",
                duration: 3000
            });
            this.shenfen = "";
        }
      } else if (this.shenfen == "裁判员") {
        if(this.jiaoyan(this.referee)){
            var list = JSON.parse(localStorage.getItem('referee')) || [];
            list.push(this.referee);
            list = JSON.stringify(list);
            localStorage.setItem('referee',list);
            Toast({
                message: "提交成功！",
                position: "middle",
                duration: 3000
            });
            this.shenfen = "";
        }
      } else {
        return;
      }
    },

    jiaoyan(obj) {
      for (let item in obj) {
        if (obj[item] == "") {
          MessageBox("提示", "请填写完整信息！");
          return false;
        }
      }
      return true;
    }
  }
};
</script>

<style lang="scss" scoped>
.hello {
  text-align: left;
}
.athlete,
.coach,
.referee {
  padding: 10px;
}
.card {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ebeef5;
  background-color: #fff;
  overflow: hidden;
  color: #303133;
}
.main {
  margin-top: 30px;
}
.suBtn {
  width: 100%;
  margin-top: 50px;
  text-align: center;
  padding-bottom: 50px;
}
</style>



