<template>
    <div class="box">
        <mt-header title="" v-if="shenfen != ''">
            <mt-button icon="back" slot="left" @click="backFn">返回</mt-button>
        </mt-header>
        <div class="btnbox" v-if="shenfen == ''">
            <mt-radio
                title="请选择您要看身份"
                v-model="shenfen"
                :options="shenfenList">
            </mt-radio>
        </div>
        <div class="main" v-if="shenfen == '运动员'">
            <p>共有{{list.length}}参与调查</p>
            <el-table
                :data="list"
                style="width: 100%">
                <el-table-column
                    prop="xingbie"
                    label="性别">
                </el-table-column>
                <el-table-column
                    prop="xueli"
                    label="学历">
                </el-table-column>
                <el-table-column
                    prop="rank"
                    label="等级">
                </el-table-column>
                <el-table-column
                    prop="factor"
                    label="因素">
                </el-table-column>
            </el-table>
        </div>

        <div class="main" v-if="shenfen == '教练员'">
            <p>共有{{list.length}}参与调查</p>
            <el-table
                :data="list"
                style="width: 100%">
                <el-table-column
                    prop="xingbie"
                    label="性别">
                </el-table-column>
                <el-table-column
                    prop="xueli"
                    label="学历">
                </el-table-column>
                <el-table-column
                    prop="rank"
                    label="等级">
                </el-table-column>
                <el-table-column
                    prop="fangfa"
                    label="方法">
                </el-table-column>
            </el-table>
        </div>

        <div class="main" v-if="shenfen == '裁判员'">
            <p>共有{{list.length}}参与调查</p>
            <el-table
                :data="list"
                style="width: 100%">
                <el-table-column
                    prop="xingbie"
                    label="性别">
                </el-table-column>
                <el-table-column
                    prop="xueli"
                    label="学历">
                </el-table-column>
                <el-table-column
                    prop="rank"
                    label="等级">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                shenfen: "",
                shenfenList: ["运动员", "教练员", "裁判员"],
                list: []
            }
        },

        created () {
            
        },

        methods: {
            backFn() {
                this.shenfen = "";
            },
        },

        watch: {
            shenfen (val) {
                console.log(val)
                if(val == '运动员'){
                    this.list = JSON.parse(localStorage.getItem('athlete')) || [];
                }else if(val == '教练员'){
                    this.list = JSON.parse(localStorage.getItem('coach')) || [];
                }else if(val == '裁判员'){
                    this.list = JSON.parse(localStorage.getItem('referee')) || [];
                }else {
                    return;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
.box{
    text-align: left;
}
.main{
    padding: 10px;
}
</style>


